/* Global styles */
body {
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
  transition: background 0.5s ease, color 0.5s ease;
}

/* Light Theme */
.light {
  background: linear-gradient(135deg, #e0eafc, #cfdef3);
  color: #222;
}

/* Dark Theme */
.dark {
  background: linear-gradient(135deg, #232526, #414345);
  color: #eee;
}

/* Main Container styles (spans the whole screen) */
.main-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh; /* Ensure it takes up the full height */
  transition: background 0.5s ease;
}

/* Header and Footer styles */
header, footer {
  padding: 1rem 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 2px solid rgba(255, 255, 255, 0.2);
  background: linear-gradient(90deg, #ffffff, #aac7ff, #ffffff, #a9c7ff, #ffffff);
  transition: background 0.5s ease;
}

/* Dark Mode Header/Footer Gradient */
.dark header, .dark footer {
  background: linear-gradient(90deg, #2c2c2c, #444444, #2c2c2c, #333333, #2c2c2c);
}

/* Button styles */
button {
  padding: 0.6rem 1.2rem;
  background: linear-gradient(135deg, #89f7fe, #66a6ff);
  border: none;
  border-radius: 25px;
  font-size: 1rem;
  color: #fff;
  cursor: pointer;
  transition: background 0.3s ease;
}

/* Dark Mode Button Color */
button.switch-dark {
  background: linear-gradient(135deg, #000000, #006aff);
}

/* Button hover effect */
button:hover {
  background: linear-gradient(135deg, #66a6ff, #89f7fe);
}

button.switch-dark:hover {
  background: linear-gradient(135deg, #006aff, #000000);
}

/* Save Button Styles */
button.save {
  background: linear-gradient(135deg, #ff7a00, #ffbd00);
  padding: 0.6rem 1.2rem;
  border-radius: 25px;
  font-size: 1rem;
  color: #fff;
  transition: background 0.3s ease;
}

/* Dark Mode Save Button Color */
.dark button.save {
  background: linear-gradient(135deg, #ff5c00, #d63b00);
}

button.save:hover {
  background: linear-gradient(135deg, #ffbd00, #ff7a00);
}

.dark button.save:hover {
  background: linear-gradient(135deg, #d63b00, #ff5c00);
}

/* Input and Form styles */
form {
  margin-top: 2rem;
}

input {
  padding: 0.6rem 1rem;
  font-size: 1rem;
  border: 2px solid #ccc;
  border-radius: 8px;
  outline: none;
  transition: border 0.3s;
}

input:focus {
  border-color: #66a6ff;
}
